.modal-container {
  width: 100%;
  min-height: 100vh;
  display: flex;
  justify-content: flex-start;
  /* align-items: center; */
  margin-top: 1rem;
  position: absolute;
  top: 0;
  left: 0;
}

.modal-wrapper {
  /* top: 0;
  right: 0; */
  min-height: 100vh;
  width: 100%;
  background: #fff;
  border-radius: 5px;
  /* padding: 1rem; */
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
}

.toast-container {
  background: rgba(0, 0, 0, 0.7);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  position: fixed;
  align-items: center;
  z-index: 3;
}

.toast-wrapper {
  height: 500px;
  width: 50%;
  background: #fff;
  border-radius: 5px;
  /* padding: 1rem; */
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  position: relative;
}

.modal-wrapper .go-back-btn {
  position: absolute;
  right: 20px;
  top: 20px;
}
.toast-wrapper svg {
  position: absolute;
  right: 20px;
  top: 20px;
  cursor: pointer;
  height: 20px;
  width: 20px;
}

.modal-title {
  text-align: center;
  font-size: 2rem;
}

.modal-input {
  height: 2rem;
  width: 50%;
  background: rgb(232, 241, 255);
  border: none;
  border-radius: 5px;
  padding: 0.5rem;
  margin: 1rem 0;
}

.form-group {
  display: flex;
  border: 1px solid #0f1975;
  border-radius: 5px;
  padding: 0.5rem;
  width: 50%;
}
.form-group label {
  margin-right: 1rem;
}
.modal-textarea {
  height: 75px;
  width: 50%;
  background: rgb(232, 241, 255);
  border: none;
  border-radius: 5px;
  padding: 0.5rem;
  margin: 1rem 0;
}
.select-box {
  height: 3rem;
  width: 51%;
  background: rgb(232, 241, 255);
  border: none;
  border-radius: 5px;
  padding: 0.5rem;
  margin: 1rem 0;
}
.form-select-group {
  display: flex;
  width: 50%;
  justify-content: space-between;
  align-items: center;
}

.checkbox-group {
  display: flex;
  justify-content: space-between;
  margin: 1rem;
}

.checkbox-subgroup {
  display: flex;
  justify-content: space-between;
  margin: 0.5rem 3rem;
}

.checkbox-group input {
  height: 20px;
  width: 20px;
}

.admin-access-options {
  width: 50%;
  padding: 1rem;
  background: rgb(232, 241, 255);
}

.news-modal-content {
  width: 60%;
  margin: 2rem auto;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}
.news-content-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 1rem;
}
.news-content-item p {
  margin-left: 2rem;
  width: 70%;
}

.tag-div {
  display: flex;
}

.tag-list {
  display: flex;
}

.tags {
  font-size: smaller;
  margin: 0 5px;
  padding: 5px 5px;
  background-color: rgb(232, 241, 255);
  display: flex;
}
.modal-wrapper .main-div {
  max-width: 95%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  overflow-x: auto;
}
.modal-wrapper .main-div .table {
  width: 100%;
  border-right-color: #ffffff;
  font-size: 0.95rem !important;
  border-collapse: collapse;
  margin: 3rem auto;
  box-shadow: 0 11px 11px 0 rgba(0 ,0 ,0 ,0.16);
}

.modal-wrapper .main-div table tbody tr td {
  padding: 7px !important;
  /* width: 100%; */
  /* display: flex; */
}

.customerDetail-main {
  overflow-x: auto;
}
.user-main {
  overflow-x: auto;
}
.comments-modal {
  width: 80%;
  margin: 2rem auto;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}
.comments-modal .modal-title {
  text-align: left;
}
.comments-modal .card {
  position: relative;
  display: flex;
  flex-direction: column;
  min-width: 0;
  word-wrap: break-word;
  background-color: #fff;
  border: 1px solid rgba(0,0,0,.125);
  border-radius: 0.25rem;
}
.comments-modal .card .card-body{
  -webkit-box-flex: 1;
  -ms-flex: 1 1 auto;
  flex: 1 1 auto;
  padding: 1.25rem;
}
.mb-13 {
   margin-bottom: 13px;
}
.mt-0 {
  margin-top: 0;
}
.mb-0 {
  margin-bottom: 0;
}
.dum {
  display: flex;
  width: 50%;
  position: relative;
}
.dum .error {
  bottom: 0;
  position: absolute;
  transform: translate(0px, 8px);
  font-size: 11px;
  color: red;
  margin-top: 0;
  margin-bottom: 0;
}

.dum .form-group {
  width: 100%;
  margin-top: 0;
  margin-bottom: 0;
}
.dum .form-group input {
  width: 50%;
}
.dum input {
  width: 100%;
}
.dum .select-box {
  width: 100%;
}
.dum .text-editor-container {
  width: 100%;
  margin: 3rem 0rem 3rem 0rem;
}
.dum .form-group {
  margin: 1rem 0;
}