@import url(https://fonts.googleapis.com/css2?family=Miltonian&family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;1,100;1,200;1,300;1,400;1,500;1,600;1,700&display=swap);
body {
  margin: 0;
  font-family: "Montserrat", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
  background: rgb(232, 241, 255);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.navbar {
    display: flex;
    justify-content: space-between;
    color: white;
    background-color: #1d2aa7;
    height: 4rem;
}

.navbar div {
    margin-top: auto;
    margin-bottom: auto;
    padding-left: 2rem;
}

.navbar div ul {
    display: flex;
    list-style-type: none;
}

.navbar div ul li {
    padding-right: 2rem;
    color: white;
}

.head {
  background: rgb(232, 241, 255);
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.box {
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: flex-start;
  height: 93vh;
}
.headbox {
  margin-top: auto;
  margin-bottom: auto;
}

.subbox-button {
  display: flex;
  justify-content: center;
}

.subbox input {
  padding-left: 1rem;
  padding-right: 1rem;
  height: 2rem;
  width: 21rem;
}

.subbox-button button {
  height: 2rem;
  width: 10rem;
  margin-top: 3rem;
  margin-bottom: 8rem;
  background-color: #1d2aa7;
  color: white;
}
.logo-style-login {
  width: 300px;
  height: 200px;
  margin: 2rem auto;
  object-fit: cover;
}

.main-window {
  display: flex;
  min-height: 100vh;
  width: 100vw;
  background: rgb(232, 241, 255);
  padding: 0;
  margin: 0;
}
.main-window-right {
  width: 85%;
  position: absolute;
  top: 0;
  right: 0;
  margin: 0 auto;
}
.main-window-right-bottom {
  position: relative;
}

.dashboard-container {
  padding: 1rem;
}
.dashboard-section {
  display: flex;
  justify-content: center;
  align-items: center;
}
.dashboard-section-item {
  background: rgb(2, 0, 36);
  background: linear-gradient(
    61deg,
    rgba(2, 0, 36, 1) 0%,
    rgba(8, 37, 140, 1) 0%,
    rgba(9, 9, 121, 1) 18%,
    rgba(0, 212, 255, 1) 100%
  );
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#020024",endColorstr="#00d4ff",GradientType=1);
  border-radius: 10px;
  padding: 1rem;
  margin: 1rem;
  width: 100%;
  /* width: 20vw; */
  height: 200px;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  box-shadow: 0 11px 11px 0 rgba(0, 0, 0, 0.16);
}

.dashboard-section-item:nth-child(1) {
  background-image: linear-gradient(
    109.6deg,
    rgba(24, 138, 141, 1) 11.2%,
    rgba(96, 221, 142, 1) 91.1%
  );
}
.dashboard-section-item:nth-child(2) {
  background-image: linear-gradient(
    132.6deg,
    rgba(71, 139, 214, 1) 23.3%,
    rgba(37, 216, 211, 1) 84.7%
  );
}

.dashboard-section-item:nth-child(3) {
  background-image: linear-gradient(
    68.3deg,
    rgba(245, 177, 97, 1) 0.4%,
    rgba(236, 54, 110, 1) 100.2%
  );
}
.dashboard-section-item .dashboard-item-icon {
  /* background: rgb(75, 184, 235); */
  border-radius: 50%;
  padding: 1rem;
  height: 50px;
  width: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  border: #fff solid 2px;
}
.dashboard-item-icon svg {
  height: 50px;
  width: 50px;
  color: #fff;
}

.dashboard-section-item h3 {
  color: #fff !important;
}
.dashboard-section-item p {
  color: #fff !important;

  font-size: 1.5rem;

  font-weight: bold;
}
.dashboard-section-2 {
  background: #fff;
  border-radius: 10px;
  padding: 1rem;
  margin: 1rem;
  box-shadow: 0 11px 11px 0 rgba(0, 0, 0, 0.16);
}

table {
  width: 100%;
  border-right-color: #ffffff;
  margin-top: 100px;
  font-size: 0.95rem !important;
  /* /* border: 1px solid #343a40; */
  border-collapse: collapse;
  margin: 3rem auto;
}

th,
td {
  /* border: 1px solid #343a40; */
  padding: 16px 24px;
  text-align: center;
}

th {
  background: #0f1975;
  color: #fff;
  width: 25% !important;
}

tbody tr:nth-child(odd) {
  background-color: #f8f9fa;
}

tbody tr:nth-child(even) {
  background-color: #e9ecef;
}

.filter-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.filter-search-bar,
.filter-select-box {
  height: 15px;
  width: 50%;
  border-radius: 5px;
  padding: 0.75rem;
  background: #fff;
  color: #000;
}

.filter-select-box {
  width: 25%;
  height: 2rem;
  padding: 0;
}

.charts-container {
  display: flex;
  margin: 1rem;
}

.area-chart {
  background: #fff;
  border-radius: 10px;
  padding: 1rem;
  width: 70%;
  margin-bottom: 2rem;
  box-shadow: 0 11px 11px 0 rgba(0, 0, 0, 0.16);
}

.pie-chart {
  background: #fff;
  border-radius: 10px;
  padding: 1rem;
  width: 30%;
  margin-left: 1rem;
  margin-bottom: 2rem;
  box-shadow: 0 11px 11px 0 rgba(0, 0, 0, 0.16);
}

.links-wrapper {
  display: flex;
  flex-direction: column;
  background: #0f1975;
  padding: 1rem;
  top: 0;
  position: fixed;
  width: 15%;
  font-weight: bold;
  min-height: 100vh;
  z-index: 2;
  padding: 0 !important;
  margin: 0 !important;
  font-size: 0.9rem;
}
.links-list {
  overflow-y: auto;
  max-height: 85vh;
}
.links-list::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background-color: #f5f5f5;
}

.links-list::-webkit-scrollbar {
  width: 10px;
  background-color: #f5f5f5;
}

.links-list::-webkit-scrollbar-thumb {
  background-color: #000000;
  border: 2px solid #555555;
}

.active .link-group {
  background: #1d2aa7;
}
.links-wrapper .links-list > a {
  color: white;
  text-decoration: none;
}
.link-group {
  display: flex;
  align-items: center;
  transition: all 0.3s ease;
  padding: 1rem;
}
.link-group svg {
  margin-right: 1rem;
}
.link-group:hover {
  background: #1d2aa7;
}
.logo-style {
  width: 180px;
  height: 100px;
  margin: 1rem auto;
  background: #fff;
  object-fit: cover;
}

.top-nav-container {
  background: white;
  width: 100%;
  height: 3rem;
  padding: 0.5rem;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  font-size: 0.9rem;
}

.user-container {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 4rem;
}

.user-container svg {
  margin-right: 1rem;
}

.add-button {
  background: #0f1975;
  border-radius: 5px;
  color: #fff;
  padding: 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-weight: bold;
  border: none;
  cursor: pointer;
  font-size: 0.8rem !important;

  min-width: 150px;
}
.save-button {
  background: green;
  border-radius: 5px;
  color: #fff;
  padding: 1rem;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: bold;
  border: none;
  cursor: pointer;
  min-width: 100px;
  margin: 1rem;
  font-size: 0.8rem !important;
}
.view-button {
  background: #1d2aa7;

  border-radius: 5px;
  color: #fff;
  padding: 1rem;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: bold;
  border: none;
  cursor: pointer;
  min-width: 100px;
  margin: 1rem 0;
  margin-right: 1rem;
  font-size: 0.8rem !important;
}
.delete-button {
  background: red;

  border-radius: 5px;
  color: #fff;
  padding: 1rem;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: bold;
  border: none;
  cursor: pointer;
  min-width: 100px;
  margin: 1rem 0;
  font-size: 0.8rem !important;

  margin-right: 1rem;
}
.add-button svg {
  margin-right: 1rem;
}

.access-container {
  padding: 1rem;
  min-height: 60vh;
}
.access-section {
  background: #fff;
  border-radius: 10px;
  padding: 1rem;
  height: 100%;
}
.access-section-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.role-list {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 1rem;
  margin: 1rem;
}
.role-item {
  background: rgb(208, 220, 231);
  padding: 1rem;
  border: 2px solid #0f1975;
  border-radius: 5px;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}
.button-group {
  display: flex;
}

.user-avatar {
  background: rgb(180, 185, 190);
  border-radius: 50%;
  padding: 1rem;
  height: 100px;
  width: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.user-avatar svg {
  height: 50px;
  width: 50px;
}

.categories-container {
  padding: 1rem;
}

.categories-list {
  background: #fff;
  border-radius: 10px;
  padding: 1rem;
}

.categories-list-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.category-item-list {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 1rem;
}
.category-item {
  border: 2px solid #0f1975;
  background: rgb(208, 220, 231);
  border-radius: 5px;
  padding: 0.5rem;
  margin: 1rem auto;
  display: flex;
  flex-direction: column;
  min-height: 200px;
  justify-content: space-evenly;
  font-weight: bold;
  align-items: center;
  width: 80%;
  font-size: 1.35rem;
}

.category-item svg {
  cursor: pointer;
}

.subcategory-container {
  width: 70%;
  margin: 0 auto;
}

.subcategory-item {
  margin: 1rem 0;
}

.button-group {
  display: flex;
}

table {
  width: 100%;
  border-right-color: #ffffff;
