.modal-container {
  width: 100%;
  min-height: 100vh;
  display: flex;
  justify-content: flex-start;
  /* align-items: center; */
  margin-top: 1rem;
  position: absolute;
  top: 0;
  left: 0;
}

.modal-wrapper {
  /* top: 0;
  right: 0; */
  min-height: 100vh;
  width: 100%;
  background: #fff;
  border-radius: 5px;
  /* padding: 1rem; */
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
}

.toast-container {
  background: rgba(0, 0, 0, 0.7);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  position: fixed;
  align-items: center;
  z-index: 3;
}

.toast-wrapper {
  height: 500px;
  width: 50%;
  background: #fff;
  border-radius: 5px;
  /* padding: 1rem; */
