  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  position: relative;
}

.modal-wrapper .go-back-btn {
  position: absolute;
  right: 20px;
  top: 20px;
}
.toast-wrapper svg {
  position: absolute;
  right: 20px;
  top: 20px;
  cursor: pointer;
  height: 20px;
  width: 20px;
}

.modal-title {
  text-align: center;
  font-size: 2rem;
}

.modal-input {
  height: 2rem;
  width: 50%;
  background: rgb(232, 241, 255);
