.add-button {
  background: #0f1975;
  border-radius: 5px;
  color: #fff;
  padding: 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-weight: bold;
  border: none;
  cursor: pointer;
  font-size: 0.8rem !important;

  min-width: 150px;
}
.save-button {
  background: green;
  border-radius: 5px;
  color: #fff;
  padding: 1rem;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: bold;
  border: none;
  cursor: pointer;
  min-width: 100px;
  margin: 1rem;
  font-size: 0.8rem !important;
}
.view-button {
  background: #1d2aa7;

  border-radius: 5px;
  color: #fff;
  padding: 1rem;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: bold;
  border: none;
  cursor: pointer;
  min-width: 100px;
  margin: 1rem 0;
  margin-right: 1rem;
  font-size: 0.8rem !important;
}
.delete-button {
  background: red;

  border-radius: 5px;
  color: #fff;
  padding: 1rem;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: bold;
  border: none;
  cursor: pointer;
  min-width: 100px;
  margin: 1rem 0;
  font-size: 0.8rem !important;

  margin-right: 1rem;
}
.add-button svg {
  margin-right: 1rem;
}
